import { useInView } from "react-hook-inview";
import { Drop, StyledValueCard } from "../Styles/Components/ValueCard";



type Props = {
    content: string;
    number: number;
}

export default function ValueCard({ content, number }: Props) {

    const [ref, inView] = useInView();

    return (
        <>
            <StyledValueCard as='li' ref={ref} className={inView ? 'active' : ''}>
                <div>
                    <span>{number}</span>
                </div>
                <Drop >

                    <div>

                        <span>{content}</span>
                    </div>
                </Drop>

            </StyledValueCard>
        </>
    );
};
