import styled, { css } from "styled-components";
import { FlexList } from "./General.styled";


const DropsAmount = 4;
//creates opacity delay for each drop
function createDelay() {
    let styles = '';

    for (let i = 0; i < DropsAmount; i++) {
        styles += `
         &:nth-of-type(${i + 1}) {
           transition-delay: ${i * .25}s;
         }
       `
    }

    return css`${styles}`;
}

const bigNumColor = "#b6cbde";

export const StyledValueCards = styled(FlexList)`

gap: 3rem;
width: fit-content;
margin: auto;

@media screen and (max-width: 864px) {
    flex-direction: column;
}


`;




export const StyledValueCard = styled.div`
    display: flex;
    flex-direction: column;

    opacity: 0;

    &.active{
        opacity: 1;
    }


    &>div:first-of-type{
        margin-bottom: 3rem;
        font-size: 7rem;
        color: ${bigNumColor};

        @media screen and (max-width: 768px) {
            margin-bottom: 5rem;
        }
    }
    &>div:last-of-type{
        margin: auto;
        padding: 1em;

    }

    transition: all 1.5s ease-in-out;
    @media screen and (min-width: 768px){

        ${createDelay()};

     
    } 

`;





