
import { StyledNavigationLink, StyledNavigationList } from "../Styles/Components/Nav.styled";
import { Link } from "../types/base";


type Props = {
    links: Link[]
}

export default function NavList({ links }: Props) {



    return (
        <>
            <StyledNavigationList id='nav'>
                <ul>

                    {links.map((link: Link, i) => (
                        <li key={i}>

                            <StyledNavigationLink to={link.url}
                                className={({ isActive, isPending, isTransitioning }) =>
                                    [
                                        isPending ? "pending" : "",
                                        isActive ? "active" : "",
                                        isTransitioning ? "transitioning" : "",
                                    ].join(" ")
                                }>{link.text}</StyledNavigationLink>
                        </li>
                    )
                    )}
                </ul>
            </StyledNavigationList>
        </>
    );
};
