import NavList from "../Components/NavList";
import { StyledNavigation } from "../Styles/Components/Nav.styled";
import { Link } from "../types/base";
import { ReactComponent as Burger } from "../assets/burger.svg"
import { useState } from "react";



type Props = {
    links: Link[]
}

export default function Nav({ links }: Props) {

    const [openMobile, setOpenMobile] = useState(false);



    return (
        <>
            <StyledNavigation>
                <Burger className={`burger ${!openMobile ? "" : "open"}`} width={40} onClick={() => setOpenMobile((b) => !b)} />
                <NavList links={links} />
            </StyledNavigation>

        </>
    );
};
